import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectAnimation, EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"


export const PathBlinkPropSchema = EffectPropSchema.extend({
  reverseDirection: z.boolean()
      .default(false)
      .describe("Reverse animation direction (default: false)"),
  aniTime: z.number()
      .default(1)
      .describe("Animation time for the path (default: 1)"),
  displayTime: z.number()
      .default(3)
      .describe("Display time for the path (default: 3)"),
  ease: z.string()
      .default('power1.out')
      .describe("Ease of animation (default: power1.out)"),
})

export class PathBlinkTemplate extends EffectTemplate<typeof PathBlinkPropSchema> {
  constructor() {
    super({
      name: "PathBlink",
      packageName: "gaia.path",
      imports: [],
      targetType: "Line",
      propSchema: PathBlinkPropSchema,
    })
  }

  create(cxt: ResolveContext<unknown, unknown>, props: z.output<typeof PathBlinkPropSchema>): PathBlinkEffect {
    const {name, packageName, targetType, propSchema} = this.meta
    return new PathBlinkEffect(name, packageName, targetType, propSchema, props)
  }
}

export class PathBlinkEffect extends EffectAnimation<typeof PathBlinkPropSchema> {
  computeDuration(cxt: ResolveContext<unknown, unknown>): number {
    return this.timing.duration ?? this.getBlinkDuration()
  }

  getBlinkDuration(): number {
    return this.prop('aniTime') * 2 + this.prop('displayTime')
  }
}
