import {gsap} from "gsap"
import {CustomEase} from "gsap/CustomEase"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import assert from "assert"
import {TypePropSchema} from "@/preset/template/text/TypeTemplate"


export class TypeImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof TypePropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // target
    const elements = this.elements
    // timing
    const stagger = this.stagger
    // effect
    const {effect} = this.props()

    const tl = gsap.timeline()

    elements.forEach((el, i) => {
      assert(el instanceof SVGTextElement, `TypeImpl: TypeImpl only support SVGTextElement, but got: ${el}`)
      const tspanList = this.splitText(el)
      let tw
      switch (effect) {
        case 'fade': tw = this.generateFade(tspanList, cxt); break
        case 'show': tw = this.generateShow(tspanList, cxt); break
        default: throw new Error(`TypeImpl: unknown effect: ${effect}`)
      }
      tl.add(tw, i === 0 ? 0 : ('<' + stagger))
    })

    return tl
  }

  splitText(textEl: SVGTextElement): SVGTSpanElement[] {
    const text = textEl.textContent
    assert(text, `TypeImpl: text is empty: ${text}`)

    if (textEl.childElementCount > 0) {
      // already split
      return Array.from(textEl.children) as SVGTSpanElement[]
    }

    textEl.textContent = ''
    const chars = text.split('')
        .filter(char => char !== ' ' && char !== '\n')

    const tspanList: SVGTSpanElement[] = []

    chars.forEach(char => {
      const tspan = document.createElementNS('http://www.w3.org/2000/svg', 'tspan')
      tspan.textContent = char
      textEl.appendChild(tspan)
      tspanList.push(tspan)
    })

    return tspanList
  }

  generateFade(tspanList: SVGTSpanElement[], cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // timing
    const duration = this.duration
    // effect
    const {type, typeStagger, ease} = this.props()

    let tw: GsapAnimation
    const vars = this.mergeYoyo({ease, stagger: typeStagger})
    switch (type) {
      case 'in':
        tw = gsap.fromTo(tspanList, {opacity: 0}, {opacity: 1, ...vars})
        break
      case 'out':
        tw = gsap.fromTo(tspanList, {opacity: 1}, {opacity: 0, ...vars})
        break
      default:
        // never goes here as type is checked by zod
        throw new Error(`Unknown type. Expect in or out, but got: ${type}`)
    }
    // the duration is applied to the whole animation (after stagger)
    tw.totalDuration(duration)

    // console.log('>>>', tspanList, type, effect, typeStagger, ease, vars, tw)

    return tw
  }

  generateShow(tspanList: SVGTSpanElement[], cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // timing
    const duration = this.duration
    // effect
    const {type, typeStagger} = this.props()

    const ease = CustomEase.create("custom", "M0,0 C0,0.24 0,1 0.012,1 0.076,1 0.78,1 1,1 ")

    let tw: GsapAnimation
    const vars = this.mergeYoyo({ease, stagger: typeStagger})
    switch (type) {
      case 'in':
        tw = gsap.fromTo(tspanList, {opacity: 0}, {opacity: 1, ...vars})
        break
      case 'out':
        tw = gsap.fromTo(tspanList, {opacity: 1}, {opacity: 0, ...vars})
        break
      default:
        // never goes here as type is checked by zod
        throw new Error(`Unknown type. Expect in or out, but got: ${type}`)
    }
    // the duration is applied to the whole animation (after stagger)
    tw.totalDuration(duration)

    // console.log('>>>', tspanList, type, effect, typeStagger, ease, vars, tw)

    return tw
  }
}

