import {AniUnitSpec} from "@/parser/json/animation/AniUnitSpec"
import {TargetSpec} from "@/parser/json/animation/TargetSpec"
import {YoyoSpec} from "@/parser/json/animation/YoyoSpec"
import {z} from "zod"

export const NumberParamSpec = z.union([
  z.number(),
  z.object({
    private: z.boolean().default(false),
    type: z.literal('number').default('number'),
    value: z.number(),
  }),
  z.object({
    private: z.boolean().default(false),
    type: z.literal('number'),
    value: z.number().optional(),
  }),
])

export const StringParamSpec = z.union([
  z.string(),
  z.object({
    private: z.boolean().default(false),
    type: z.literal('string').default('string'),
    value: z.string(),
  }),
  z.object({
    private: z.boolean().default(false),
    type: z.literal('string'),
    value: z.string().optional(),
  })
])


export const AnimationParamSpec = z.union([
  AniUnitSpec,
  z.object({
    private: z.boolean().default(false),
    type: z.literal('aniUnit').default('aniUnit'),
    value: AniUnitSpec,
  }),
  z.object({
    private: z.boolean().default(false),
    paramType: z.literal('aniUnit'),
    value: AniUnitSpec.optional(),
  })
])

export const TargetParamSpec = z.union([
  TargetSpec,
  z.object({
    private: z.boolean().default(false),
    type: z.literal('target').default('target'),
    value: TargetSpec,
  }),
  z.object({
    private: z.boolean().default(false),
    type: z.literal('target'),
    value: TargetSpec.optional(),
  })
])

export const YoyoParamSpec = z.union([
  YoyoSpec,
  z.object({
    private: z.boolean().default(false),
    type: z.literal('yoyo').default('yoyo'),
    value: YoyoSpec,
  }),
  z.object({
    private: z.boolean().default(false),
    type: z.literal('yoyo'),
    value: YoyoSpec.optional(),
  })
])

export const ParamSpec = z.union([
  NumberParamSpec,
  StringParamSpec,
  AnimationParamSpec,
  TargetParamSpec,
  YoyoParamSpec,
])


export type NumberParamSpec = z.infer<typeof NumberParamSpec>
export type StringParamSpec = z.infer<typeof StringParamSpec>
export type AnimationParamSpec = z.infer<typeof AnimationParamSpec>
export type TargetParamSpec = z.infer<typeof TargetParamSpec>
export type YoyoParamSpec = z.infer<typeof YoyoParamSpec>

export type ParamSpec = z.infer<typeof ParamSpec>


