import {LogicalAnimation} from "@/core/ast/animation/logical/LogicalAnimation"
import {ResolveRule} from "@/core/exec/resolve/ResolveRule"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"
import {LogicalAnimationUtils} from "@/core/ast/animation/logical/utils/LogicalAnimationUtils"
import {UnaryAnimation} from "@/core/ast/animation/logical/UnaryAnimation"
import {AryAnimation} from "@/core/ast/animation/logical/AryAnimation"
import {LeafAnimation} from "@/core/ast/animation/logical/LeafAnimation"
import {LoggerManager} from "@/core/utils/Logger"

/**
 * Resolves target and set target selection for all animations.
 *
 * Grouping operations (e.g., group) should be processed before it.
 */
export class ResolveTarget extends ResolveRule {
  private static readonly logger = LoggerManager.getLogger("ResolveTarget")

  constructor() {
    super("ResolveTarget")
  }

  apply(cxt: ResolveContext<unknown, unknown>, input: LogicalAnimation<any>): LogicalAnimation<any> {
    const model = cxt.targetModel

    let sel = model.rootSelection
    for (const trans of input.target.transforms) {
      const newSel = trans.execute(sel)
      ResolveTarget.logger.trace(`Converting [${sel}] to [${newSel}]`)
      sel = newSel
    }
    input.target.selection = sel
    ResolveTarget.logger.debug(`Execute ${input.target.transforms.length} transform(s), for ROOT -> ${input}, `
        + `converting [${model.rootSelection}] to [${sel}]`)

    LogicalAnimationUtils.preorderTraversal(input, node => {
      if (node instanceof UnaryAnimation) {
        this.applyTransform(node, node.child)
      } else if (node instanceof AryAnimation) {
        for (const child of node.children) {
          this.applyTransform(node, child)
        }
      } else if (node instanceof LeafAnimation) {
        // do nothing
      }
      return false
    })

    return input
  }

  applyTransform(parent: LogicalAnimation<any>, child: LogicalAnimation<any>) {
    let sel = parent.target.selection
    for (const trans of child.target.transforms) {
      const newSel = trans.execute(sel)
      ResolveTarget.logger.trace(`${trans} executed, converting [${sel}] to [${newSel}]`)
      sel = newSel
    }
    ResolveTarget.logger.debug(`Execute ${child.target.transforms.length} transform(s) for ${parent} -> ${child}, `
        + `converting [${parent.target.selection}] to [${sel}]`)
    child.target.selection = sel
  }
}
